import React from "react";
import { NavLink, Redirect, Route, Switch } from "react-router-dom";
import About from "./About";
import Contact from "./Contact";
import Home from "./Home";
import Service from "./Service";

const Navbar =()=>{
    return(
    <>
  
    <div className="container-fluid nav_bg">
        <div className="row">
            <div className="col-10 mx-auto">
    <nav className="navbar navbar-expand-lg navbar-light ">
  <div className="container-fluid">
    <NavLink className="navbar-brand" to="/">Umer Tech</NavLink>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <NavLink activeClassName="nav_btn" exact className="nav-link " to="/">Home</NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName="nav_btn" exact className="nav-link" to="/service">Services</NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName="nav_btn" exact className="nav-link" to="/about">About</NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName="nav_btn" exact className="nav-link" to="/contact">Contact</NavLink>
        </li>
        </ul>
    </div>
  </div>
</nav>
</div>
</div>
</div>
  <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/service" component={Service} />
        <Route exact path="/about" component={About} />
        <Route exact path="/contact" component={Contact} />
        <Redirect to="/"/>
    </Switch>
    </>);
}
export default Navbar;